import React, { useState, useEffect } from 'react';

const Donate = () => {
  const [selectedAmount, setSelectedAmount] = useState(25);
  const [customAmount, setCustomAmount] = useState('');
  const [paymentMethod, setPaymentMethod] = useState('creditCard');
  const [donorName, setDonorName] = useState('');
  const [donorEmail, setDonorEmail] = useState('');
  const [showSpendingBreakdown, setShowSpendingBreakdown] = useState(true);

  const handleAmountSelect = (amount) => {
    setSelectedAmount(amount);
    if (amount !== 'custom') {
      setCustomAmount('');
    }
  };

  const handlePaymentMethodSelect = (method) => {
    setPaymentMethod(method);
  };

  const handleDonateSubmit = (e) => {
    e.preventDefault();
    
    // Validate form
    if (!donorName || !donorEmail) {
      alert('Please fill in all required fields');
      return;
    }
    
    const amount = selectedAmount === 'custom' ? customAmount : selectedAmount;
    
    if (!amount || amount <= 0) {
      alert('Please enter a valid donation amount');
      return;
    }
    
    // Process donation
    alert(`Thank you for your donation of $${amount}! We will process your payment shortly.`);
    
    // Reset form
    setSelectedAmount(25);
    setCustomAmount('');
    setDonorName('');
    setDonorEmail('');
  };

  const spendingBreakdown = [
    { category: 'Education Programs', percentage: 35, color: '#0b6b6b', description: 'Workshops, training materials, and educational resources for women' },
    { category: 'Legal Support', percentage: 25, color: '#ff6b6b', description: 'Legal aid, counseling, and representation for women in need' },
    { category: 'Emergency Aid', percentage: 20, color: '#4ecdc4', description: 'Immediate support for women in crisis situations' },
    { category: 'Advocacy Campaigns', percentage: 15, color: '#45b7d1', description: 'Awareness campaigns and policy change initiatives' },
    { category: 'Operational Costs', percentage: 5, color: '#96ceb4', description: 'Essential administrative and organizational expenses' }
  ];

  return (
    <section id="donate" className="donate-section py-5">
      <div className="container">
        <div className="text-center mb-5">
          <h2 className="section-title text-white mb-3">Empower Women Through Your Support</h2>
          <p className="lead text-white mb-4">
            Every donation creates lasting change in the lives of women and girls worldwide
          </p>
          <div className="row justify-content-center">
            <div className="col-lg-8">
              <div className="impact-badge">
                <div className="row">
                  <div className="col-md-4">
                    <div className="impact-item">
                      <i className="fas fa-users fa-2x mb-2"></i>
                      <h4 className="mb-1">500+</h4>
                      <small>Women Supported</small>
                    </div>
                  </div>
                  <div className="col-md-4">
                    <div className="impact-item">
                      <i className="fas fa-graduation-cap fa-2x mb-2"></i>
                      <h4 className="mb-1">10</h4>
                      <small>Programs Launched</small>
                    </div>
                  </div>
                  <div className="col-md-4">
                    <div className="impact-item">
                      <i className="fas fa-globe fa-2x mb-2"></i>
                      <h4 className="mb-1">8</h4>
                      <small>Countries Reached</small>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div className="row justify-content-center">
          <div className="col-lg-10">
            <div className="card shadow donation-card">
              <div className="card-body p-4">
                <div className="row">
                  {/* Donation Amount Selection */}
                  <div className="col-lg-6">
                    <h4 className="card-title mb-4">Choose Your Impact</h4>
                    
                    <div className="donation-options mb-4">
                      <div className="row g-3">
                        {[
                          { amount: 25, impact: 'Educational materials for 5 women', icon: '📚' },
                          { amount: 50, impact: 'Weekly advocacy support', icon: '📢' },
                          { amount: 100, impact: 'Legal aid for one case', icon: '⚖️' },
                          { amount: 'custom', impact: 'Custom impact amount', icon: '💝' }
                        ].map((item, index) => (
                          <div className="col-6" key={index}>
                            <div
                              className={`donation-option ${selectedAmount === item.amount ? 'selected' : ''}`}
                              onClick={() => handleAmountSelect(item.amount)}
                            >
                              <div className="option-icon">{item.icon}</div>
                              <h5>{item.amount === 'custom' ? 'Custom' : `$${item.amount}`}</h5>
                              <small>{item.impact}</small>
                            </div>
                          </div>
                        ))}
                      </div>

                      {selectedAmount === 'custom' && (
                        <div className="custom-amount mt-3">
                          <label htmlFor="customAmount" className="form-label">Enter Your Amount</label>
                          <div className="input-group">
                            <span className="input-group-text">$</span>
                            <input
                              type="number"
                              className="form-control"
                              id="customAmount"
                              value={customAmount}
                              onChange={(e) => setCustomAmount(e.target.value)}
                              placeholder="Enter amount"
                              min="1"
                              required
                            />
                          </div>
                        </div>
                      )}
                    </div>

                    {/* Spending Breakdown */}
                    <div className="spending-breakdown">
                      <div className="d-flex justify-content-between align-items-center mb-3">
                        <h5 className="mb-0">How Your Donation Helps</h5>
                        <button
                          type="button"
                          className="btn btn-link p-0"
                          onClick={() => setShowSpendingBreakdown(!showSpendingBreakdown)}
                        >
                          {showSpendingBreakdown ? 'Hide Details' : 'Show Details'}
                        </button>
                      </div>
                      
                      {showSpendingBreakdown && (
                        <div className="breakdown-content">
                          {spendingBreakdown.map((item, index) => (
                            <div key={index} className="breakdown-item mb-3">
                              <div className="d-flex justify-content-between mb-1">
                                <span className="breakdown-category">{item.category}</span>
                                <span className="breakdown-percentage">{item.percentage}%</span>
                              </div>
                              <div className="progress mb-2" style={{ height: '8px' }}>
                                <div 
                                  className="progress-bar" 
                                  style={{ 
                                    width: `${item.percentage}%`,
                                    backgroundColor: item.color
                                  }}
                                ></div>
                              </div>
                              <small className="text-muted">{item.description}</small>
                            </div>
                          ))}
                          <div className="transparency-note">
                            <i className="fas fa-shield-alt me-2"></i>
                            We are committed to full financial transparency. Annual reports available upon request.
                          </div>
                        </div>
                      )}
                    </div>
                  </div>

                  {/* Payment Information */}
                  <div className="col-lg-6">
                    <form onSubmit={handleDonateSubmit}>
                      <h5 className="mb-4">Complete Your Donation</h5>

                      {/* Donor Information */}
                      <div className="mb-4">
                        <div className="row g-3">
                          <div className="col-12">
                            <label htmlFor="name" className="form-label">Full Name *</label>
                            <input
                              type="text"
                              className="form-control"
                              id="name"
                              value={donorName}
                              onChange={(e) => setDonorName(e.target.value)}
                              placeholder="Enter your full name"
                              required
                            />
                          </div>
                          <div className="col-12">
                            <label htmlFor="email" className="form-label">Email Address *</label>
                            <input
                              type="email"
                              className="form-control"
                              id="email"
                              value={donorEmail}
                              onChange={(e) => setDonorEmail(e.target.value)}
                              placeholder="Enter your email"
                              required
                            />
                          </div>
                        </div>
                      </div>

                      {/* Payment Method */}
                      <div className="payment-options mb-4">
                        <label className="form-label mb-3">Payment Method</label>
                        <div className="row g-3">
                          {[
                            { id: 'creditCard', label: 'Credit Card', icon: 'fa-credit-card', methods: ['visa', 'mastercard', 'amex'] },
                            { id: 'paypal', label: 'PayPal', icon: 'fa-paypal' },
                            { id: 'bankTransfer', label: 'Bank Transfer', icon: 'fa-university' }
                          ].map((method) => (
                            <div className="col-12" key={method.id}>
                              <div 
                                className={`payment-method ${paymentMethod === method.id ? 'selected' : ''}`}
                                onClick={() => handlePaymentMethodSelect(method.id)}
                              >
                                <div className="form-check mb-0">
                                  <input
                                    className="form-check-input"
                                    type="radio"
                                    name="paymentMethod"
                                    id={method.id}
                                    checked={paymentMethod === method.id}
                                    onChange={() => {}}
                                    required
                                  />
                                  <label className="form-check-label w-100" htmlFor={method.id}>
                                    <div className="d-flex align-items-center">
                                      <i className={`fas ${method.icon} fa-2x me-3`}></i>
                                      <div>
                                        <h6 className="mb-1">{method.label}</h6>
                                        {method.methods && (
                                          <div className="payment-icons">
                                            {method.methods.map((card, idx) => (
                                              <i key={idx} className={`fab fa-cc-${card} me-1`}></i>
                                            ))}
                                          </div>
                                        )}
                                      </div>
                                    </div>
                                  </label>
                                </div>
                              </div>
                            </div>
                          ))}
                        </div>
                      </div>

                      {/* Donation Summary */}
                      <div className="donation-summary mb-4">
                        <div className="summary-item d-flex justify-content-between">
                          <span>Donation Amount:</span>
                          <strong>${selectedAmount === 'custom' ? customAmount : selectedAmount}</strong>
                        </div>
                        <div className="summary-item d-flex justify-content-between">
                          <span>Processing Fee:</span>
                          <span>Free</span>
                        </div>
                        <hr />
                        <div className="summary-total d-flex justify-content-between">
                          <span>Total Amount:</span>
                          <strong>${selectedAmount === 'custom' ? customAmount : selectedAmount}</strong>
                        </div>
                      </div>

                      {/* Submit Button */}
                      <button type="submit" className="btn btn-primary btn-lg w-100 donate-btn-2">
                        <i className="fas fa-heart me-2"></i>
                        Donate Now
                      </button>

                      <div className="text-center mt-3">
                        <small className="text-muted">
                          <i className="fas fa-lock me-1"></i>
                          Your donation is secure and tax-deductible
                        </small>
                      </div>
                    </form>
                  </div>
                </div>
              </div>
            </div>

            {/* Additional Info */}
            <div className="row mt-4">
              <div className="col-md-6">
                <div className="info-card">
                  <i className="fas fa-hand-holding-heart"></i>
                  <h5>Recurring Donations</h5>
                  <p>Set up monthly donations to provide sustained support for our ongoing programs.</p>
                </div>
              </div>
              <div className="col-md-6">
                <div className="info-card">
                  <i className="fas fa-building"></i>
                  <h5>Corporate Matching</h5>
                  <p>Many employers match charitable contributions. Double your impact today.</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Donate;