import React from 'react';
import { useNavigate, useLocation } from 'react-router-dom';

const Hero = () => {
  const navigate = useNavigate();
  const location = useLocation();

  const handleSupportClick = (e) => {
    e.preventDefault();
    
    // If we're not on the home page, navigate to home first
    if (location.pathname !== '/') {
      navigate('/');
      setTimeout(() => {
        const donateSection = document.getElementById('donate');
        if (donateSection) {
          donateSection.scrollIntoView({ behavior: 'smooth', block: 'start' });
        }
      }, 500);
    } else {
      // We're already on home page, just scroll
      const donateSection = document.getElementById('donate');
      if (donateSection) {
        donateSection.scrollIntoView({ behavior: 'smooth', block: 'start' });
      }
    }
  };

  return (
    <section className="hero">
      <div className="container">
        <h1 className="animate__animated animate__fadeInDown">Progressive Forces of Afghanistan</h1>
        <p className="animate__animated animate__fadeIn animate__delay-1s">
          Defending women's rights, promoting gender equality, and fighting for
          justice
        </p>
        <a 
          href="#donate" 
          className="btn btn-primary btn-lg mt-4 animate__animated animate__fadeInUp animate__delay-2s"
          onClick={handleSupportClick}
        >
          Support Our Cause
        </a>
      </div>
    </section>
  );
};

export default Hero;